import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import SpeedDialIcon from "@mui/material/SpeedDialIcon";

const dispatchedEvents = [];

const SrsMuiSpeedDialIcon = function (props, ref) {
  return (
    <SpeedDialIcon
      icon={props.icon}
      openIcon={props.openIcon}
      ariaLabel={props["aria-label"]}
      theme={props.muiTheme}
    />
  );
};

const styles = `
  /* Custom styles for the MuiSpeedDialIcon component */
`;

const props = [
  {
    name: "icon",
    defaultValue: null,
    type: "string",
  },
  {
    name: "openIcon",
    defaultValue: null,
    type: "string",
  },
  {
    name: "aria-label",
    defaultValue: null,
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiSpeedDialIcon),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
